@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@mixin f {
  font-family: "Roboto", sans-serif; }
@mixin bg {
  background: linear-gradient(110.27deg, rgba(255, 255, 255, 0.08) 0%, rgba(255, 255, 255, 0) 74.43%), linear-gradient(180deg, #1144C1 0%, #002BB1 100%); }
@mixin skew {
  transform: skewX(-36deg); }

body {
  min-width: 1260px;
  background: #E5E5E5;
  @include f;
  font-size: 14px;
  line-height: 1.3;
  &.no-scroll {
    overflow: hidden; } }
a {
  color: #316AFE;
  text-decoration: none; }

.center {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 30px; }

.hero {
  position: relative;
  margin-bottom: -40px;
  background-color: #2C3038;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 2;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(black,.2);
    z-index: -2; }
  &__top {
    position: relative;
    height: 48px;
    z-index: 2;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 18px;
      right: 18px;
      height: 100%;
      background: white;
      @include skew;
      z-index: -2; } }
  &__container {
    display: flex;
    min-height: 392px;
    padding: 40px 0 90px; }
  &__title {
    margin: auto;
    font-size: 48px;
    font-weight: 700;
    color: white;
    line-height: (56/48); } }

.btn {
  display: inline-block;
  position: relative;
  min-width: 144px;
  padding: 11px 32px;
  background: none;
  font-size: 16px;
  font-weight: 900;
  color: white;
  text-align: center;
  transition: opacity .2s;
  line-height: 1.15;
  z-index: 2;
  &:hover {
    opacity: .7; }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 15px;
    right: 15px;
    height: 100%;
    @include bg;
    @include skew;
    z-index: -2; }
  &_white {
    color: #35497C;
    &:before {
      background: white; } } }

.header {
  position: relative;
  z-index: 10;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: -40px;
    right: calc(50% - 598px);
    height: 100%;
    @include bg;
    @include skew;
    z-index: -2; }
  &__center {
    display: flex;
    min-height: 80px;
    align-items: center; }
  &__logo {
    margin-right: auto; }
  &__pic {
    display: block; }
  &__nav {
    display: flex;
    height: 80px;
    margin-right: 30px;
    line-height: 80px; }
  &__link {
    font-size: 16px;
    color: #AABBE5;
    font-weight: 700;
    transition: box-shadow .2s, color .2s;
    @include nl {
      margin-right: 44px; }
    &:hover {
      color: white; }
    &.active {
      box-shadow: 0 4px 0 0 white inset;
      color: white; } }
  &__btn {
    margin-right: -15px; } }

.shape {
  position: absolute;
  top: 0;
  left: calc(50% - 120px);
  right: -210px;
  height: 576px;
  background: #3A3f48;
  transform: skewX(36deg);
  overflow: hidden;
  z-index: -2;
  &__bg {
    position: absolute;
    top: 0;
    left: -210px;
    right: -210px;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 50%;
    @include skew; }
  &_left {
    left: auto;
    right: calc(50% - 150px);
    left: -210px;
    transform: skewX(-36deg); }
  &_left &__bg {
    transform: skewX(36deg); } }

.title {
  margin-bottom: 16px;
  font-size: 24px;
  font-weight: 900;
  color: #35497C;
  line-height: (28/24);
  text-align: center;
  text-transform: uppercase; }

.content {
  font-size: 16px;
  color: rgba(#4E4E4E,.9);
  line-height: (26/16); }

.about {
  position: relative;
  margin-top: -40px;
  padding: 80px 0;
  overflow: hidden;
  &__row {
    display: flex;
    margin: 0 -18px; }
  &__col {
    padding: 0 18px;
    flex: 0 0 50%;
    &:last-child {
      padding-top: 276px; } }
  &__card {
    padding: 114px 56px 106px;
    background: white;
    text-align: center; }
  &__preview {
    margin-bottom: 32px;
    font-size: 0; }
  &__more {
    margin-top: 76px; }
  &__text {
    max-width: 440px;
    margin-bottom: 30px;
    color: #35497C;
    font-weight: 700;
    line-height: (23/14); } }

.quote {
  position: relative;
  overflow: hidden;
  &__center {
    display: flex;
    height: 576px;
    align-items: center;
    justify-content: flex-end; }
  &__content {
    position: relative;
    max-width: 880px;
    padding: 66px 200px 62px 240px;
    font-size: 20px;
    font-style: italic;
    font-weight: 700;
    color: white;
    line-height: (33/20);
    z-index: 2;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 110px;
      right: 110px;
      height: 100%;
      @include bg;
      @include skew;
      z-index: -2; }
    &:after {
      content: '';
      position: absolute;
      top: 35px;
      left: 200px;
      width: 140px;
      height: 102px;
      background: url(../img/quote.svg) no-repeat center / contain;
      z-index: -1; } } }

.masters {
  padding-top: 80px;
  &__heading {
    margin-bottom: 80px;
    font-weight: 700;
    text-transform: uppercase; }
  &__row {
    display: flex;
    margin: 0 -18px; }
  &__col {
    margin: 0 18px 80px;
    background: white;
    text-align: center;
    flex: 0 0 calc(50% - 36px); }
  &__preview {
    display: block;
    background: #262C35;
    overflow: hidden; }
  &__preview:hover &__pic {
    transform: scale(1.05); }
  &__pic {
    display: block;
    width: 100%;
    transition: transform .4s; }
  &__wrap {
    padding: 56px 56px 50px; }
  &__title {
    display: inline-block;
    text-transform: none;
    transition: opacity .2s;
    &:hover {
      opacity: .7; } } }

.footer {
  min-height: 120px;
  @include bg; }

.field {
  &__input,
  &__area {
    width: 100%;
    background: #FCFCFC;
    border: 2px solid #EFEFEF;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    color: #35497C;
    &::placeholder {
      color: rgba(#35497C,.6); } }
  &__input {
    height: 48px;
    padding: 0 18px; }
  &__area {
    height: 198px;
    padding: 14px 18px; } }

.form {
  padding: 104px 0 100px;
  text-align: center;
  &__container {
    max-width: 800px;
    margin: 0 auto;
    padding: 64px 200px 0;
    background: white;
    box-shadow: 0px 59px 122px rgba(0, 0, 0, 0.042);
    border-radius: 16px; }
  &__preview {
    margin-bottom: 24px;
    font-size: 0; }
  &__title {
    margin-bottom: 48px; }
  &__row {
    display: flex;
    margin: 0 -12px; }
  &__col {
    padding: 0 12px;
    flex: 0 0 50%; }
  &__field {
    margin-bottom: 24px; }
  &__action {
    margin-top: 48px;
    font-weight: 700; }
  &__foot {
    position: relative;
    top: 19px;
    margin-top: 45px; } }







